import "./assets/css/accordion.css!";
import {EventEmitter} from "../core/event-emitter";
import {EBasicColorStatus} from "../component/enum/e-basic-color-status";
import {AContainer} from "./abstract/a-container";
import {Box} from "./box";
import {Form} from "./form";
import {AccordionItem,IConfigAccordionItem} from "./accordion-item";

import jquery = require("jquery");

export type IContainerItemAccordion = Form<any>| Box;

export interface IConfigAccordion{
	colorIcon?:string;
	collapseIcon?:string;
	expandIcon?:string;
	iconPosition?:"left"|"right";
	titleColor?:string;
	titleRender?:string;
	animationSpeed?:number|string;
	isOpen?:boolean;
}

/**
 * Este container agrupa um conjunto de containers.
 * 
 * <img src="../../doc_assets/accordion-example.png">
 * 
 * @example
 * import {Accordion, Box} from "mvcomponents/container";
 * import {Label} from "mvcomponents/widget";
 * import {LoginForm} from "./login-form";
 *
 * let myAccordion: Accordion = new Accordion()
 *      .append(new LoginForm(),{title:"teste"})
 *      .append(new Box(),{"title":"Titulo Accordion B"})
 *      .append(new Box(),{titleRender:'<i style="float:left;margin:0px 5px 5px 0px;font-size:25px;color:#2ecca2" class="mv-basico-dados_demograficos"></i><label>Custom Title</label>',iconPosition:"right"})
 * 
 * @export
 * @memberof module:mvcomponents/container
 * @extends {AContainer}
 */
export class Accordion extends AContainer<IContainerItemAccordion>{
	/**
	 * Evento disparado toda vez que um item do accordion é recolhido
	 * @returns {EventEmitter}
	 */
	public onCollapse:EventEmitter<IConfigAccordionItem> = new EventEmitter();
	/**
	 * Evento disparado toda vez que um item do accordion é expandido
	 */
	public onExpand:EventEmitter<IConfigAccordionItem> = new EventEmitter();
	private itens:AccordionItem[];
	private config:IConfigAccordionItem;
	constructor(config?:IConfigAccordion){
		super("div", "");
		this.addStyleName("accordion-box");	
		this.itens = [];
		this.config = {titleColor:"#555",colorIcon:"#555",animationSpeed:"fast",expandIcon:"mv-basico-seta_baixo_simples",collapseIcon:"mv-basico-seta_cima_simples",isOpen:false};
		if(config){
			this.config = Object.assign({},this.config,config); 
		}
	}
	private appendContainer(item:IContainerItemAccordion,config:IConfigAccordionItem,where:string):this{
		let configResolved:IConfigAccordionItem = Object.assign({},this.config,config);
		//let tmpContainer:Panel = new Panel(config.title);
		let tmpContainer:AccordionItem = new AccordionItem({
			title:config.title
			,expandIcon:configResolved.expandIcon
			,collapseIcon:configResolved.collapseIcon
			,colorIcon:configResolved.colorIcon
			,disabled:configResolved.disabled
			,iconPosition:configResolved.iconPosition
			,titleColor:configResolved.titleColor
			,animationSpeed:configResolved.animationSpeed
			,isOpen:configResolved.isOpen
			,titleRender:configResolved.titleRender
		});	

		this.itens.push(tmpContainer);	

		tmpContainer.onExpand.subscribe(config=> {
			let tmpconfig = Object.assign({},config);
			this.onExpand.emit(tmpconfig).done(() => {               
				if(!tmpconfig.disabled){
					tmpContainer.expand();
				}
			});
		});

		tmpContainer.onCollapse.subscribe(config=> {
			let tmpconfig = Object.assign({},config);
			this.onCollapse.emit(tmpconfig).done(() => {               
				if(!tmpconfig.disabled){
					tmpContainer.collapse();
				}
			});
		});

		tmpContainer.setSize(12).addStyleName("accordion-container");
		item.addStyleName("accordion-item");
		tmpContainer
			.body
			.append(item);	
		
		this.insertItem(<any>tmpContainer,where);
		if(configResolved.isOpen){
			tmpContainer.expand();
		}else{
			tmpContainer.collapse();
		}
		return this;
	}

	/**
	 * Adiciona um item no accordion abaixo dos outros itens
	 * @returns {Accordion}
	 */	
	public append(item:IContainerItemAccordion,config?:IConfigAccordionItem):this{
		this.appendContainer(item,config,"append");
		return this;
	}

	/**
	 * Adiciona um item no accordion acima dos outros itens
	 * @returns {Accordion}
	 */    
	public prepend(item:IContainerItemAccordion,config?:IConfigAccordionItem):this{
		this.appendContainer(item,config,"prepend");
		return this;
	}

	/**
	 * Expande todos os itens, mesmo os que estão desabilitado
	 * @returns {Accordion}
	 */    
	public expandAll():this{
		this.itens.forEach(item=>item.expand());
		return this;
	}

	/**
	 * Recolhe todos os itens, mesmo os que estão desabilitado
	 * @returns {Accordion}
	 */    
	public collapseAll():this{
		this.itens.forEach(item=>item.collapse());
		return this;
	}

	/**
	 * Expande um item pelo indice, mesmo que esteja desabilitado
	 * @param {number} indice
	 * @returns {Accordion}
	 */    
	public expandIndex(index:number):this{
		if(index > -1 && index < this.itens.length){
			this.itens[index].expand();
		}
		return this;
	}

	/**
	 * Recolhe um item pelo indice, mesmo que esteja desabilitado
	 * @param {number} indice
	 * @returns {Accordion}
	 */ 
	public collapseIndex(index:number):this{
		if(index > -1 && index < this.itens.length){
			this.itens[index].collapse();
		}
		return this;
	}

	public removeAllItens():this{
		this.itens.forEach(item => {
			item.onAttached.unsubscribeAll();
			item.onCollapse.unsubscribeAll();
			item.onExpand.unsubscribeAll();
			//item.onBeforeDestroy.emit(true);
			//item.destroy();
		});        
		this.itens = [];
		jquery(this.element).children().detach();
		return this;
	}

}
